@media screen and (max-width: 767px) {
    .header-tagline {
		span {
			color: $black;
			font-size: 14px;
			font-weight: 400;
		}
	}
	.box-container {
		padding: 25px 10px 20px;
	}
	.primary {
		padding: 0;
		h2 {
			font-size: 22px;
		}
	}
	.filter-block {
		margin-top: 10px;
	}
	.form-control {
		&.tel-code {
			width: 30%;
		}
		&.tel-number {
			width: 60%;
		}
	}
	.checkbox-label {
		span {
			width: 90%;
		}
	}
	.form-group.dates {
		margin-top: 15px;
	}
	.form-b-m {
		padding: 0;
		margin-top: 15px;
	}
	//Medicare Plans in Your Area
	.medicare-in-your-area {
		padding-top: 30px;
		h2 {
			font-size: 20px;
			margin-bottom: 40px;
		}
		ul {
			float: none;
			@include columns(2 5em);
		}
	}
	.books-section h5 {
		margin-top: 25px;
	}
	.compare-plans-block {
		margin-top: 10px;
	}
	//FOOTER
	.contact-block {
		margin-top: 25px;
	}
	.top-footer-inner-pages {
		ul {
			li {
				display: block;
				&:first-child {
					a {
						&:before {
							content: "|";
						}
					}
				}
			}
			&.socials {
				li {
					display: inline-block;
					a {
						&:before {
							content: normal;
						}
					}
				}
			}
		}
	}
	.table-responsive {
		border: none;
	}
	.inner-secondary {
		margin-top: 25px;
	}
	.schedule {
		.table {
			min-width: 235px;
		}
	}
}
@media (min-width: 768px) and (max-width: 1024px) {
	.logos-section {
		ul {
			li {
				float: left;
				width: 20%;
			}
		}
	}

	.navbar-default .navbar-nav > li a {
		font-size: 13px;
	}

	button.close {
		i {
			left: 0;
		}
	}
	.books-section h5 {
		 @include ellipsis(100%);
	}
	footer hgroup h2 {
		font-size: 25px;
	}
	.schedule {
		.table {
			min-width: 235px;
		}
	}
}