* {
    &:focus {
        outline: none!important;
    }
}
html{
	position: relative;
    height: 100%; 
}
body{
	margin: 0;
	padding: 0;
    min-height: 100%; 
    height: 100%; 
    font-family: $arial;
}
a {
    cursor: pointer;
    &:hover {
        text-decoration: none;
    }
}

p{
	margin: 0;
	padding: 0;
}
ul, li {
    list-style: none;
    padding: 0;
    margin: 0;
}
.container-fluid {
    padding: 0;
}
main {
    padding-bottom: 50px;
}
.wrapper {
    margin-bottom: 80px;
}




