footer {
	position: relative;
	font-family: $verdana;
	&:after {
		content: "";
		@include linear-gradient(#328dcd 0, #217cbb 100%);
		position: absolute;
		top: 30px;
		bottom: 0;
		left: 0;
		right: 0;
		z-index: -1;
	}
	color: $white;
	hgroup {
		h4 {
			color: $snow;
			font-family: $arial;
			font-size: 14px;
			font-weight: 400;
			margin: 0;
		}
		h2 {
			color: $snow;
			font-family: $arial;
			font-size: 36px;
			font-weight: 700;
			margin: 5px 0 20px;
		}
	}
}
.top-footer {
	padding: 15px 0 20px;
	-webkit-box-shadow: 0px 8px 8px 0px rgba(0,0,0,0.75);
	-moz-box-shadow: 0px 8px 8px 0px rgba(0,0,0,0.75);
	box-shadow: 0px 8px 8px 0px rgba(0,0,0,0.75);
	@include linear-gradient(#328dcd 0, #217cbb 100%);
	p, h3 {
		color: $white;
		font-family: $verdana;
		font-size: 10px;
		font-weight: 400;
		line-height: 16px;
	}
	h3 {
		margin: 0;
		font-weight: 600;
	}
}
.top-footer-inner-pages {
	padding: 35px 0 10px;
	ul {
		padding-bottom: 20px;
		li {
			display: inline-block;
			a {
				color: $white;
				line-height: 1;
				font-weight: normal;
				font-family: $verdana;
				&:before {
					content: "|";
					padding-right: 5px;
				}
			}
			&:first-child {
				a {
					&:before {
						content: normal;
					}
				}
			}
		}
		&.socials {
			margin-top: 20px;
			padding-bottom: 0;
			li {
				a {
					&:before {
						content: normal;
					}
				}
			}
		}
	}
}
.copyright {
	background: $blue;
	p {
		color: $white;
		font-family: $verdana;
		font-size: 12px;
		font-weight: 400;
		margin: 5px 0;
		@media screen and (min-width: 768px) {
			line-height: 40px;
			margin: 0;
		}
	}
}
.copyright-text {
	padding-left: 0;
}
.socials {
	li {
		display: inline-block;
		margin-right: 10px;
		height: 100%;
		padding-top: 4px;
		box-sizing: border-box;
		a {
			display: inline-block;
			width: 34px;
			height: 33px;
			background: url("../img/socials-sprite.png") 0 0 no-repeat;
			&:hover {
				opacity: 0.9;
			}
			&.facebook {
				background-position: left top;
			}
			&.linkedin {
				background-position: center top;
			}
			&.twitter {
				background-position: right top;
			}
		}
	}
}