.banner-section {
	background-image: url("../img/banner.png");
	min-height: 463px;
	border-bottom: 10px solid $blue;
    background-position: center top;
    background-repeat: no-repeat;
    background-size: cover;
}
.box-container {
	max-width: 436px;
	margin-top: 68px;
	background-color: $white;
    @include border-radius(10px);
    -web-kit-box-shadow: -1px 6px 20px 3px $grey;
    -moz-box-shadow: -1px 6px 20px 3px $grey;
    box-shadow: -1px 6px 20px 3px $grey;
    border: 1px solid $grey;
    padding: 25px 25px 20px;
    margin-bottom: 1em;
    h2 {
    	color: $color-theme-second;
		font-family: $verdana;
		font-size: 24px;
		font-weight: 700;
		margin: 0;
    }
    h3 {
    	margin-top: 15px;
    	font-size: 18px;
    }
    .subtext {
    	font-family: $helveticaLight;
		font-size: 18px;
		line-height: 20px;
		padding: 20px 0 12px;
    }
    form {
    	padding-bottom: 10px;
    }
}
.zip-code-content {
	display: table;
	margin: 0 auto;
	.zip-code, .find-plans {
		display: table-cell;
		vertical-align: top;
	}
}
.mask {
	overflow: hidden;
    height: 1px;
    &:after {
    	content: "";
    	display: block;
		margin: -25px auto 0;
		width: 100%;
		height: 25px;
		-web-kit-border-radius: 125px/12px;
		-moz-border-radius: 125px/12px;
		border-radius: 125px/12px;
		-web-kit-box-shadow: 0 0 8px $grey;
		-moz-box-shadow: 0 0 8px $grey;
		box-shadow: 0 0 8px $grey;
    }
}

//LOGOS SECTION
.logos-section {
	text-align: center;
	.owl-carousel-logos {
		display: inline-block;
		padding: 50px 0 40px;
		overflow: hidden;
		div {
			display: inline-block;
			vertical-align: middle;
		}
	}
	ul {
		li {
			display: inline-block;
		}
	}
}

//MEDIACARE PLANS
.medicare-plans-section {
	padding: 30px 0 0;
	.medicare-plans {
		padding-bottom: 45px;
	}
	h3 {
		a {
			font-weight: 400;
	    	font-size: 19px;
	    	color: #06c;
		}
	}
	p {
		font-size: 16px;
		margin-bottom: 15px;
		@media screen and (min-width: 1200px) {
			padding: 0 45px;
		}
	}
}
//Medicare Services
.medicare-services-section {
	color: $black;
	margin-bottom: 30px;
	.container {
		@media screen and (min-width: 1200px) {
			padding: 65px 60px 30px;
		}
	}
	h2 {
		font-size: 30px;
		color: $black;
		font-family: $helveticaLight;
		font-weight: 400;
		padding-bottom: 10px;
		@media screen and (min-width: 768px) {
			font-size: 36px;
		}
	}
	article {
		font-weight: 400;
		h3 {
			font-family: $helveticaLight;
			margin-bottom: 0;
		}
		a {
			text-decoration: underline;
			color: #094784;
			&:hover {
				text-decoration: none;
			}
		}
	}
}
//TESTIMONIALS
.testimonials-section {
	margin-bottom: 40px;
	h3 {
		padding: 10px 25px;
		font-size: 38px;
	}
	p {
		font-style: italic;
		padding: 0 25px;
		font-size: 16px;
		span {
			padding: 15px 0;
			text-align: right;
			width: 100%;
			display: inline-block;
		}
	}
}
//Medicare Plans in Your Area
.medicare-in-your-area {
	h2 {
		font-size: 25px;
		margin-bottom: 40px;
		margin-top: 10px;
	}
	img {
		width: 100%;
	}
	ul {
		float: right;
		@include columns(4 10em);
		li {
			a {
				color: $black;
				font-family: $verdana;
				line-height: 25px;
				&:hover {
					text-decoration: underline;
				}
			}
		}
	}
	.list-of-states {
		padding-right: 0;
	}
}
.form-b {
	.logos-section {
		ul {
			padding: 10px 0;
		}
	}
}
.mediacare-resources-top {
	padding-top: 30px;
	.zip-code-content {
		float: right;
	}
	.compare-plans-block {
		span {
			display: inline-block;
			margin-bottom: 5px;
			font-weight: bold;
		}
	}
}
.section-form {
	padding-bottom: 100px;
}
.check-icon {
	color: black;
	font-family: $verdana;
	font-size: 14px;
	font-weight: 700;
	>span {
		display: inline-block;
		float: left;
	}
	&:before {
		content: "";
		width: 27px;
		height: 29px;
		display: inline-block;
		float: left;
		margin-right: 10px;
		background: url("../img/check-icon.png");
	}
}
.books-section {
	margin-bottom: 100px;
	.row {
		padding: 45px 0;
	}
	h5 {
		text-align: center;
		color: $black;
		font-size: 25px;
		font-family: $helveticaLight;
	}
}
//MODAL

@media (min-width: 768px) {
	.modal-dialog {
	    width: 740px;
	    margin: 30px auto;
	}
}
.modal-body {
	padding: 0;
}
.modal-content {
	padding: 15px;
	// &-wrapper {
	// 	border: 1px solid #c3cfdd;
	// 	@include border-radius(10px);
	// }
}
.form-header {
	border: 1px solid #c3cfdd;
	border-bottom: 0;
	@include border-radius(10px 10px 0 0);
	position: relative;
	padding: 15px 0;
	h4 {
		margin-top: 0;
	}
}
.form-heading {
	/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#104372+0,15568f+100 */
	background: rgb(16,67,114); /* Old browsers */
	background: -moz-linear-gradient(top,  rgba(16,67,114,1) 0%, rgba(21,86,143,1) 100%); /* FF3.6-15 */
	background: -webkit-linear-gradient(top,  rgba(16,67,114,1) 0%,rgba(21,86,143,1) 100%); /* Chrome10-25,Safari5.1-6 */
	background: linear-gradient(to bottom,  rgba(16,67,114,1) 0%,rgba(21,86,143,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#104372', endColorstr='#15568f',GradientType=0 ); /* IE6-9 */
	color: $white;
	margin: 10px 0;
	@include border-radius(5px 5px 0 0);
	font-size: 22px;
	padding: 5px 15px;
	margin: 0px;
}
.section-content {
	padding: 25px;
	background: #f8fcfe;
	border: 1px solid #c3cfdd;
	border-bottom: 0;
	@include border-radius(0px);
	&.section-content-addr {
		margin-bottom: 34px;
		@include border-radius(0 0  10px 10px);
		border: 1px solid #c3cfdd;
	}
}
.applicant {
	.section-content {
		border: 1px solid #c3cfdd;
		padding-bottom: 50px;
	}
}
.find-plans {
	.modal-content {
	    background-color: #fff;
	    -webkit-border-radius: 10px;
	    -moz-border-radius: 10px;
	    -ms-border-radius: 10px;
	    border-radius: 10px;
	    -web-kit-box-shadow: -1px 6px 20px 3px #CCC;
	    -moz-box-shadow: -1px 6px 20px 3px #CCC;
	    box-shadow: -1px 6px 20px 3px #CCC;
	    border: 1px solid #CCC;
	    padding: 25px 25px 20px;
	    .box-container {
	    	margin: 0;
	    	padding: 0;
	    }
	}
	@media (min-width: 768px) {
		.modal-dialog {
		    width: 436px;
		    margin: 30px auto;
		}
	}

	.modal-header {
		padding: 0;
		border: none;
	}
	.box-container {
		max-width: 100%;
		padding: 30px 0;
		border: none;
		box-shadow: none;
		margin-top: 30px;
	}
	button.close {
		top: -15px;
		right: -15px;
		z-index: 10;
	}
}
.modal {
  text-align: center;
  padding: 0!important;
}

.modal:before {
  content: '';
  display: inline-block;
  height: 100%;
  vertical-align: middle;
  margin-right: -4px; /* Adjusts for spacing */
}

.modal-dialog {
  display: inline-block;
  text-align: left;
  vertical-align: middle;
}
