.table {
	position: relative;
	min-width: 700px;
	span {
		display: block;
	}
	label {
		display: block;
	}
	.btn {
		display: block;
	}
	a {
		display: block;
	}
	thead {
		>tr {
			>th {
				background: #f6f6f6;
				vertical-align: middle;
				position: relative;
				border-bottom: 2px solid $white;
				font-family: $verdana;
				font-weight: 400;
				&:after {
					content: "";
					width: 1px;
					height: 100%;
					position: absolute;
					right: 0px;
					bottom: 0;
					top: 0;
					background: $white;
				}
				&:before {
					content: "";
					width: 1px;
					height: 100%;
					position: absolute;
					right: 1px;
					bottom: 0;
					top: 0;
					background: #ddd;
				}
			}
			&:first-child {
				>th {
					&:last-child {
						&:before, &:after {
							content: normal;
						}
					}
				}
			}
			&:last-child {
				border: none;
				box-shadow: none;
				>th {
					background: none;
					border-top: 2px solid $white!important;
					position: relative;
					&:before {
						content: normal;
					}
					&:after {
						content: "";
						position: absolute;
						width: 100%;
						bottom: 0;
						left: 0;
						right: 0;
						padding: 0;
						height: 5px;
						border: none;
						/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#dddddd+0,ffffff+100&1+0,0+100 */
						background: -moz-linear-gradient(top,  rgba(221,221,221,1) 0%, rgba(255,255,255,0) 100%); /* FF3.6-15 */
						background: -webkit-linear-gradient(top,  rgba(221,221,221,1) 0%,rgba(255,255,255,0) 100%); /* Chrome10-25,Safari5.1-6 */
						background: linear-gradient(to bottom,  rgba(221,221,221,1) 0%,rgba(255,255,255,0) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
						filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#dddddd', endColorstr='#00ffffff',GradientType=0 ); /* IE6-9 */

						box-shadow: none;
					}
					&:last-child {
						border: none;
					}
				}
			}
		}
	}
	tbody {

		>tr {
			>td{
				padding: 25px 8px;
				vertical-align: middle;
				border: none;
				.f-group {
					margin-top: 10px;
				}
			}
			&:first-child {
				>td {
					border: none;
					&:first-child {
						
					}
				}
			}
			&:nth-of-type(odd) {
				background: $white;
			}
			&:nth-of-type(even) {
				background: #f6f6f6;
			}
		}
	}
}
span {
	&.active {
		i {
			color: $blue;
			display: inline-block;
			width: 10%;
			padding-right: 5px;
		}
	}
	&.inactive {
		color: #999;
		i {
			display: inline-block;
			width: 10%;
			padding-right: 5px;
		}
	}
	&.emergency {
		display: inline-block;
		padding-left: 17px;
	}
}
.compare {
	padding-left: 10px;
	color: #3380e1;
}
.view {
	display: block;
	margin-top: 15px;
	color: #3380e1;
}
