input[type="submit"], .btn {
	margin-left: -5px;
	min-width: 95px;
	padding: 4px 5px 46x;
	height: 33px;
	background-color: #6fb1eb;
	background-image: linear-gradient(to top, #549ae3 0%, #8fc4f0 100%);
	border: 1px solid #8fc4f0;
	border-left: 0;
	@include border-radius(3px);
	box-shadow: inset 1px 1.7px 0 rgba(255, 255, 255, 0.33);
	color: $black;
	font-size: 18px;
	font-weight: 400;
	@include transition(all 0.5s ease-in-out);
	&:hover {
		background-image: linear-gradient(to top, #8fc4f0  0%, #549ae3 100%);
	}
}
input[type="submit"] {
	position: relative;
}
.btn {
	height: auto;
	padding: 4px 12px;
	@include border-radius(3px);
}
.f-btn {
	min-width: 133px;
	/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#fe993d+0,f15b06+100 */
	background: rgb(254,153,61); /* Old browsers */
	background: -moz-linear-gradient(top,  rgba(254,153,61,1) 0%, rgba(241,91,6,1) 100%); /* FF3.6-15 */
	background: -webkit-linear-gradient(top,  rgba(254,153,61,1) 0%,rgba(241,91,6,1) 100%); /* Chrome10-25,Safari5.1-6 */
	background: linear-gradient(to bottom,  rgba(254,153,61,1) 0%,rgba(241,91,6,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#fe993d', endColorstr='#f15b06',GradientType=0 ); /* IE6-9 */
	color: $white;
	border: 1px solid #fa5f0d;
	position: relative;
	&:after {
		content: "";
		@include triangle(12px, $white, right);
		display: inline-block;
		vertical-align: middle;
		margin-left: 7px;
	}
	&:hover {
		/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#f15b06+0,fe993d+100 */
		background: rgb(241,91,6); /* Old browsers */
		background: -moz-linear-gradient(top,  rgba(241,91,6,1) 0%, rgba(254,153,61,1) 100%); /* FF3.6-15 */
		background: -webkit-linear-gradient(top,  rgba(241,91,6,1) 0%,rgba(254,153,61,1) 100%); /* Chrome10-25,Safari5.1-6 */
		background: linear-gradient(to bottom,  rgba(241,91,6,1) 0%,rgba(254,153,61,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f15b06', endColorstr='#fe993d',GradientType=0 ); /* IE6-9 */
		color: $white;
	}
}
button.close {
	position: absolute;
	top: -15px;
	right: -15px;
	width: 34px;
	height: 34px;
	background: $black;
	border: 3px solid $white;
	opacity: 1;
	@include border-radius(50%);
	text-align: center;
	i {
		position: relative;
		top: -2px;
		left: 0px;
		color: $white;
		line-height: 1;
		font-size: 20px;
	}
	&:hover {
		opacity: 1;
		i {
			opacity: 0.7;
		}
	}
}
.plans-a {
	button.close {
		top: -30px;
		right: -30px;
	}
}