.secondary {
	padding: 0;
	text-align: center;
	.f-group {
		margin-bottom: 5px;
	}
	.secondary-content {
		text-align: left;
		position: relative;
		border: 1px solid #e1e1e1;
		&:after {
			content: "";
			width: 3px;
			position: absolute;
			top: 0;
			bottom: 0;
			right: 0;
			background: #f6f6f6;
		}
		&-block {
			border-bottom: 1px solid #e1e1e1;
		}
	}
	h3 {
		font-size: 15px;
		font-family: $verdana;
		border-top: 1px solid #e1e1e1;
		border-bottom: 1px solid #e1e1e1;
		padding: 12px;
		margin: 0;
		&:first-child {
			border-top: 0;
		}
	}
	h4 {
		padding: 12px 12px 0;
		color: $secondary-green;
		font-size: 14px;
		cursor: pointer;
		margin: 0 0 12px;
		&:before {
			content: "";
			display: inline-block;
			vertical-align: middle;
			margin-right: 10px;
			position: relative;
			top: -2px;
			@include triangle(12px, $black, down);
		}
	}
	ul {
		padding: 0 12px;
		&.shop-by-product {
			border-bottom: 1px solid #e1e1e1;
			margin-bottom: 1px;
			margin-top: 12px;
		}
		.help-choose {
			display: inline-block;
			padding-left: 25px;
			margin-bottom: 15px;
		}
	}
	label {
		font-family: $verdana;
		font-weight: 400;
	}
	.btn {
		display: inline-block;
		margin-top: 17px;
	}
}
.inner-secondary {
	a {
		color: $red;
		font-size: 18px;
		&:hover {
			text-decoration: underline;
		}
	}
}
.schedule {
	margin-top: 30px;
	a {
		color: $black;
		font-weight: bold;
		font-size: 20px;
		&:hover {
			text-decoration: none;
		}
	}
	.table {
		min-width: 100%;
		tbody {
			>tr {
				>td {
					padding: 8px;
					&:first-child {
						padding-left: 0;
					}
				}
				&:nth-of-type(even) {
					background: none;
				}
			}
		}
	}
}
.mediacare-options-logos {
	li {
		margin-bottom: 30px;
		text-align: center;
		>div {
			margin-top: 10px;
		}
		img {
			display: block;
			text-align: center;
		}
		img, a {
			display: inline-block;
		}
	}
}