.checkbox label, .radio label {
	padding-left: 0;
}
// Style the checkbox-label if it has text inside
.checkbox-label {
  display: inline-block;
  color: #585958;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  position: relative;
  &:hover {
    cursor: pointer;
  }
  span {
  	display: inline-block;
  	float: left;
  	width: 95%;
  	line-height: 1;
  }
}
form {
  font-family: $arial;
}
input[type="checkbox"] {
  // Hide the navtive checkbox, but don't remove it form the DOM to make sure tabbing works, styling goes on the label
  &.checkbox {
    opacity: 0;
    position: absolute;
  }
  // Create the checkbox
  + .checkbox-label {
    &:before {
      font-family: 'iconicfill', sans-serif;
      content: '';
      font-size: 1em;
      background-color: transparent;
      border: 1px solid #c0c6cb;
      @include border-radius(0);
      display: inline-block;
      width: 18px;
      height: 18px;
      padding-top: 2px;
      margin-right: 5px;
      vertical-align: top;
      cursor: pointer;
      text-align: center;
      float: left;
    }
  }
  &:checked {
    + .checkbox-label {
      &:before {
        content: "\f00c";
        color: $blue;
        line-height: 22px;
        font: normal normal normal 14px/1 FontAwesome;
      }
    }
  }
  &:focus {
    + .checkbox-label {
      &:before {
        outline: none;
        border: 1px solid #c0c6cb;
      }
    }
  }
  + .checkbox-label {
    &:empty {
      &:before {
        margin-right: 0;
      }
    }
  }
}

// Radio button
// Style the radio if it has text inside
.radio-label {
    display: inline-block;
    color: #585958;
    font-size: 14px;
    font-weight: 400;
    line-height: 18px;
    position: relative;
    vertical-align: middle;
    margin-bottom: 0;
    &:hover {
        cursor: pointer;
    }
}

input[type="radio"] {
  // Hide the navtive radio, styling goes on the label
  &.radio {
    opacity: 0;
    position: absolute;
  }
  // Create the radio
  + .radio-label {
    position: relative;
    &:before {
      content: '';
      background: #fff;
      border-radius: 100%;
      border: 1px solid #c0c6cb;
      display: inline-block;
      width: 18px;
      height: 18px;
      margin-right: 0.5em;
      vertical-align: top;
      cursor: pointer;
      text-align: center;
    }
  }
  &:checked {
    + .radio-label {
      &:after {
        content: '';
        position: absolute;
        top: 4px;
        left: 4px;
        width: 10px;
        height: 10px;
        background-color: $blue;
        @include border-radius(50%);
      }
    }
  }
  &:focus {
    + .radio-label {
      &:before {
        outline: none;
        border: 1px solid #c0c6cb;
      }
    }
  }
  + .radio-label {
    &:empty {
      &:before {
        margin-right: 0;
      }
    }
  }
}
.radio-btn {
    margin: 15px 0;
    input[type="radio"] + .radio-label:before {
        margin-right: 5px;
    }
    .f-group {
        margin-right: 10px;
    }
}
.f-group {
    position: relative;
    top: -2px;
    display: inline-block;
    span {
      font-family: $arial;
    }
}

.zip-code {
  display: inline-block;
  width: auto;
  max-width: 85px;
  height: 33px!important;
  @include border-radius(5px 0 0 5px);
  box-shadow: none;
  border: 1px solid #d6d6d6;
  padding: 5px;
}
label {
	font-weight: normal;
}
.select2-container {
	width: auto!important;
	display: block;
}
.forms-inline {
	padding: 0 5px;
	label {
		display: block;
	}
}
.select2-container--default .select2-selection--single {
	@include border-radius(0);
	height: 30px;
  border: 1px solid #ccc;
  -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
  box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
	.select2-selection__rendered {
		line-height: 30px;
	}
	.select2-selection__arrow {
		height: 30px;
	}
}

input[type="radio"] {
	margin-right: 10px;
}
input[type="checkbox"] {
	margin-right: 10px;
}
.form-control {
	@include border-radius(0);
  height: 30px;
	&.tel-code {
		width: 10%;
		margin: 0 5px;
		display: inline-block;
	}
	&.tel-number {
		width: 30%;
		display: inline-block;
	}
}
.form-group {
	&.dates {
		display: block;
		width: 100%;
		label {
			display: block;
		}
		.form-control {
			display: inline-block;
			max-width: 29%;
			padding: 6px;
		}
	}
}
.radio {
	display: inline-block;
	margin-right: 15px;
}
.required {
  display: inline-block;
  color: $red;
  padding-right: 5px;
}
.form-title {
  font-size: 28px;
  font-family: $arial;
  +span {
    .required {
      padding-right: 0;
    }
  }
}
.form-logo {
  p {
    color: #0673ab;
    text-align: right;
  }
  span {
    display: block;
    font-size: 10px;
    text-align: center;
    >span {
      display: inline-block;
      color: $red;
    }
  }
}
.optional {
  color: #ddd;
}
.book-now {
	height: 200px;
}
