.navbar {
    min-height: auto;
    padding: 0;
    margin-bottom: 0;
    min-height: 34px;
}
.navbar-default {
    background: $blue;
    border: none;
    box-shadow: none;
    @include border-radius(0);
    .navbar-toggle {
        border-color: #fff;
        &:hover {
            background: transparent;
        }
        .icon-bar {
            background-color: #fff;
        }
    }
    .navbar-nav {
        margin-left: -5px;
        >li {
            padding: 8px 0;
            @include transition(all 0.5s ease-in-out);
            a {
                color: $color-menu-links;
                font-family: $verdana;
                font-size: 16px;
                font-weight: 400;
                padding: 0 15px;
                @media screen and (min-width: 768px) {
                    border-left: 1px solid $color-menu-links;
                }
                @media screen and (max-width: 767px) {
                  color: $white!important;
                }
            }
            &:hover, &:focus {
                a {
                    background: none!important;
                    color: $color-menu-links;
                }
            }
            &:first-child {
                a {
                    border: none;
                }
            }
            &:hover, &.l-active {
                background: $color-menu-links-hover;
            }
            &.dropdown {
              &:hover {
                 .dropdown-menu {
                     display: block;
                 }
              }
            }
        }

        .dropdown-menu {
          @media screen and (max-width: 767px) {
            padding-left: 20px;
            margin-top: 10px;
            margin-bottom: 0;
          }
          >li {
            a {
              display: block;
              color: $black;
              padding: 5px 10px;
              border-top: 1px solid $black;
              @media screen and (max-width: 767px) {
                padding-left: 0;
                border-top: 1px solid $white;
              }
            }
            &:first-child {
              a {
                border-top: none;
              }
            }
          }
        }
    }
}
