.primary {
	padding-right: 0;
	h2 {
		margin: 0;
		line-height: 1;
		font-family: $helveticaLight;
		font-size: 28px;
	}
}
.primary-heading {
	padding: 25px 0;
}
.filter-block {
	font-family: $helveticaLight;
	font-size: 16px;
	a {
		font-family: $verdana;
		display: inline-block;
		padding-left: 15px;
		font-size: 14px;
		i {
			color: #adadad;
		}
	}
	.select2-container {
		display: inline-block;
	}
}
.mediacare-options {
	.medicare-services-section {
		.container {
			padding: 15px;
		}
	}
}
.mediacare-basics-top {
	margin-top: 20px;
}
.mediacare-basics-middle {
	font-size: 15px;
	h2 {
		color: $white;
		background: $blue;
		padding: 15px;
		font-size: 49px;
		font-weight: bold;
		line-height: 60px;
		@media screen and (max-width: 767px) {
			font-size: 29px;
			line-height: 35px;
		}
		span {
			font-weight: normal;
			font-size: 38px;
			@media screen and (max-width: 767px) {
				font-size: 19px;
				line-height: 1;
			}
		}
	}
	h3 {
		font-size: 49px;
		@media screen and (max-width: 767px) {
			font-size: 29px;
		}
	}
	p {
		margin-bottom: 15px;
	}
	a {
		text-decoration: underline;
	}
	img {
		margin: 0 auto;
	}
	ul {
		padding-left: 15px;
		margin-bottom: 15px;
		li {
			list-style: disc!important;
			list-style-position: inside;
			ul {
				li {
					list-style: circle!important;
				}
			}
		}
	}
}
.mediacare-options-links {
	margin-top: 20px;
	li {
		list-style-type: disc;
		padding: 5px 0;
		a {
			font-size: 16px;
		}
	}
}
