.top-header {
	padding: 22px 0;
}
.header-tagline {
	float: right;
	.tel {
		color: $black;
		font-size: 36px;
		font-weight: 700;
		line-height: 1;
		display: block;
		margin-bottom: 3px;
	}
	span {
		font-family: $helveticaLight;
		color: $black;
		font-size: 18px;
		font-weight: 400;
		line-height: 1;
		+span {
			font-family: $arial;
			display: block;
			font-weight: 700;
			>span {
				font-family: $arial;
				color: $green;
				font-size: 20px;
				font-weight: 700;
				display: inline-block;
				padding-right: 5px;
			}
		}
	}
}