.form-submition {
    padding: 20px 0 40px;
    .form-submition-wr {
        max-width: 730px;
        padding: 0 15px;
    }
    h2 {
        color: #2ea0de;
        font-weight: bold;
        padding-bottom: 25px;
        border-bottom: 1px solid #d3d3d3;
    }
    h4 {
        color: #707c79;
        margin-bottom: 20px;
        font-size: 24px;
    }
    h3 {
        margin: 7px 0 20px;
        font-size: 30px;
    }
    span {
        color: #707c79;
    }
    form {
        margin: 20px 0;
        .custom-form-control {
            width: 75px;
            height: 46px;
            display: inline-block;
            border-color: #2ea0de;
            @include border-radius(4px);
            margin-right: 20px;
            @media screen and (max-width: 767px) {
                width: 50px;
            }
            &-big {
                width: 100px;
                @media screen and (max-width: 767px) {
                    width: 50px;
                }
            }
        }
    }
    a {
        color: #000;
        i {
            color: #2ea0de;
        }
    }
    sup {
        font-size: 90%;
    }
    &-content {
        height: 300px;
        overflow: hidden;
        @media screen and (max-width: 767px) {
            height: auto;
            margin-bottom: 30px;
        }
    }
    .img-container {
        text-align: right;
        img {
            display: inline-block;
        }
    }
    .btn-gr {
        text-align: center;
        margin-bottom: 20px;
    }
    .custom-btn {
        width: 100%;
        height: 65px;
        text-transform: uppercase;
        margin-bottom: 15px;
        background: #404145;
        color: #fff;
        border: none;
        box-shadow: none;
        line-height: 55px;
        &:hover {
            background: lighten(#404145, 10%);
        }
        &-call {
            background: #d9dce0;
            color: #645b58;
            &:hover {
                background: lighten(#d9dce0, 5%);
            }
        }
    }
    ul {
        li {
            display: inline-block;
            &:before {
                content: "|";
                padding: 0 5px 0 0;
            }
            &:first-child {
                &:before {
                    content: normal;
                }
            }
        }
    }
}
